@media all and (orientation: landscape) {

  .btn-see-all {
    display: none;
  }

  .btn-see-all-desktop {
    margin-right: 10px;
    font-family: 'Alata';
    color: rgb(255, 255, 255);
    background-color: rgb(0, 0, 0);
    border: solid 1.5px rgb(0, 0, 0);
    padding: 10px 60px;
    text-transform: uppercase;
    letter-spacing: 3px;
    font-size: 17px;
  }

  .our-creations {
    text-transform: uppercase;
    text-align: left;
    font-family: 'Josefin Sans';
    font-weight: 400;
    font-size: 38px;
    margin-left: 10px;
  }

  .section-creations {
    position: relative;
    top: -100px;
  }

  .container-creations {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    color: rgb(255, 255, 255)
  }

  .section-creations > div {
    display: flex;
    justify-content: space-between;
    margin-bottom: 40px;
  }

  .item-creation div{
    width: 50%;
    padding-left: 25px;
    padding-bottom: 25px;
    text-transform: uppercase;
  }

  .item-creation {
    font-size: 25px;
    font-weight: 300;
    background-size: 100% 300px;
    height: 300px;
    width: 23%;
    margin: 10px 10px;
    background-repeat: no-repeat;
    box-shadow: inset 0 0 700px black;
    display: flex;
    align-items: flex-end;
  }

  .deep-earth {
    background-image: url(../../static/media/image-deep-earth.f8da6014.jpg);
  }

  .night-arcade {
    background-image: url(../../static/media/image-night-arcade.a5acf9d0.jpg);
  }

  .soccer-team-vr {
    background-image: url(../../static/media/image-soccer-team.24ac0872.jpg)
  }

  .the-grid {
    background-image: url(../../static/media/image-grid.043c26ef.jpg);
  }

  .from-up-above-vr {
    background-image: url(../../static/media/image-from-above.f316f3f3.jpg);
  }

  .pocket-borealis {
    background-image: url(../../static/media/image-pocket-borealis.0b89db1f.jpg);
  }

  .the-curiosity {
    background-image: url(../../static/media/image-curiosity.40d51f38.jpg);
  }

  .make-it-fisheye {
    background-image: url(../../static/media/image-fisheye.8bb3f74e.jpg);
  }
}
@media all and (orientation: portrait) {
  footer {
    background-color: rgb(0, 0, 0);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 70px 0;
  }
  
  .menu-footer {
    color: rgb(255, 255, 255);
    margin: 50px 0;
  }

  .menu-footer ul li {
    text-align: center;
    padding: 18px 0;
  }
  
  .all-rights-reserved {
    color: rgb(255, 255, 255, .7);
  }

  .social-media {
    display: flex;
    justify-content: center;
  }

  .social-media img{
    margin: 0 12px;
    margin-bottom: 30px;
  }
}
@media all and (orientation: landscape) {
  footer {
    background-color: rgb(0, 0, 0);
    display: flex;
    justify-content: space-between;
    padding: 70px 7%;
  }

  .menu-footer {
    color: rgb(255, 255, 255);
    margin-top: 30px;
  }

  .menu-footer ul {
    display: flex;
  }

  .social-media img {
    padding-left: 25px;
  }

  .menu-footer ul li {
    padding-right: 40px;
  }

  .menu-footer ul li span {
    visibility: hidden;
    margin-top: 8px;
    margin-left: 10px;
    display: block;
    width: 30px;
    height: 3px;
    background-color: rgb(255, 255, 255);
  }

  .menu-footer ul li:hover span{
    visibility: visible;
  }

  .all-rights-reserved {
    margin-top: 30px;
    color: rgb(255, 255, 255, .7);
  }

  .social-media-all-rights {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-end;
  }
}
